export function getStartValue(v, stateValue) {
    if (Array.isArray(v)) {
        return v[0];
    } else {
        return stateValue;
    }
}
export function getEndValue(v) {
    if (Array.isArray(v)) {
        var b = v[1];
        return b == undefined ? v[0] : b;
    } else {
        return v;
    }
}
export function getTweenValue(v, stateValue) {
    if (Array.isArray(v)) {
        var a = v[0];
        var b = v[1];
        return [
            a,
            b == undefined ? a : b
        ];
    } else {
        return [
            stateValue,
            v
        ];
    }
}